body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #202142;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav {
  background-color: black;
  padding: 16px 32px;
}
nav a {
  margin-right: 16px;
  color: white;
}

.post-excerpt {
  background-color: lightblue;
}
.nav--post {
  background-color: green;
  padding: 5px 0px;
}
ul {
  list-style-type: none;
  margin: 5px;
  overflow: hidden;
}
li {
  float: left;
  padding: 0 5px 0 15px;
}
li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.about--section {
  color: white;
}
.dashboard-section {
  color: white;
}
.productsPage-section {
  color: white;
}
.nomatch--section {
  color: white;
}

.shop--cart {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  max-width: 200px;
  margin: auto;
  margin-top: 20px;
  text-align: center;
  font-family: arial;
  background-color: lemonchiffon;
}

.price {
  color: grey;
  font-size: 22px;
}
.cart--image {
  height: 100%;
  width: 100%;
}
.shop--cart button {
  border: none;
  outline: 0;
  padding: 12px;
  color: white;
  background-color: #000;
  text-align: center;
  cursor: pointer;
  width: 100%;
  font-size: 18px;
}

.shop--cart button:hover {
  opacity: 0.7;
}

.button {
  display: inline-block;
  background: #ea3546;
  color: white;
  border-radius: 4px;
  font-weight: 700;
  padding: 0.75rem 1.5rem;
  margin-bottom: 15px;
}

.product--gallery {
  display: flex;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  max-width: 95%;
  margin: 20px;

  font-family: arial;
  flex-wrap: wrap;

  justify-content: space-around;
  border: 5px solid black;
}

#gallery-cell {
  float: left;
  width: 18%;
  padding: 5px;
}

.discount--price--text {
  margin: 0.1em;
  padding: 0.4em;
  flex-basis: 500px;
  border: 1px dotted #ccc;
  display: inline-block;
  color: #484848;
  background-color: white;
  del {
    color: rgba(red, 0.5);
    text-decoration: none;
    position: relative;
    &:before {
      content: " ";
      display: block;
      width: 100%;
      border-top: 2px solid rgba(red, 0.8);
      height: 12px;
      position: absolute;
      bottom: 0;
      left: 0;
      transform: rotate(-7deg);
    }
  }
  ins {
    color: green;
    font-size: 32px;
    text-decoration: none;
    padding: 1em 1em 1em 0.5em;
  }
}

/* customer review form start */
.product--review--input {
  width: 50%;
  border: 5px solid;
  background-color: #f2f2f2;
  padding: 20px;
  font-family: Arial, Helvetica, sans-serif;
  margin: auto;
}

input[type="text"],
input[type="email"],
select,
textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}

input[type="submit"] {
  background-color: #04aa6d;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type="submit"]:hover {
  background-color: #45a049;
}

/* customer review form end */

.product--review--display {
  width: 50%;
  border: 5px solid;
  background-color: #f2f2f2;
  padding: 20px;
  font-family: Arial, Helvetica, sans-serif;
  margin: auto;
  margin-top: 20px;
}
#review {
  background-color: #04aa6d;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 16px;
  margin-top: 15px;
}

#search--product--page {
  margin: 15px;
  width: 95%;
}

#list--item {
  margin: auto;
  padding: 10px;
  align-items: center;
}
.ordered--list {
  margin-left: 20px;
}
ol {
  float: none;
}

.product--filter--sort {
  display: flex;
  background-color: white;
  margin: 3px;
  border-radius: 7px;
  justify-content: space-between;
}
